@import url('https://fonts.googleapis.com/css?family=Comfortaa');

header {
        min-height: 30vh;
    padding: 10vh 1vh;
}

.App {
  text-align: center;
  font-family: 'Comfortaa', cursive;
}

.App-logo {
  pointer-events: none;
  height: 33vmin;
}

.App-logo-small {
     height: 15vmin;
     align-self: flex-start;
}

.App-logo-small img{

}

.App-header {
  background-color: white;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.cities {
  display: flex;
  margin: auto;
  max-width: 1000px;
  padding-top: 50px;
}

.cities ruby {
  margin: 20px auto;
  letter-spacing: 9px;
  font-size: 20px;
  font-family: 'Comfortaa', cursive;
    font-weight: 500;
}

.cities ruby rt {
    letter-spacing: 9px;
    font-size: 15px;
    font-family: 'Comfortaa', cursive;
    font-weight: 600;
}

.cities h4 {
    font-size: 25px;
}

.btn {
  font-family: 'Comfortaa', cursive;
  border-radius: 10px;
  background-color: #282c34;
  font-size: 25px;
  margin: 40px auto 0;
  color: white;
}

footer {
    min-height: 50vh;
    /*color: #e0dddd;*/
    color: white;
    overflow: hidden;
    background-color: #3f5251;
    position: relative;
    padding-bottom: 5vh;
}

.footer-standard {
    min-height: 5vh;
    padding-bottom: 0;
    background-color: white;
    color: #3f5251;
}

.footer-standard .cities {
    padding-top: 0;
}

.footer-standard h3 {
    margin-top: -15px;
    color: #3f5251;
}

footer::after {
    content: '';
    height: 4px;
    width: 100%;
    background-color: white;
    top: 10px;
    left: 0;
    position: absolute;
}

#enter {
    cursor: pointer;
    background-color: white;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    height: 70px;
    width: 70px;
    background-image: linear-gradient(to right, #3f5251, #3f5251 50%, transparent 50%);
    background-size: 201% 100%;
    background-position: 100%;
    position: relative;
    text-decoration: none;
    color: #3f5251;
    font-family: 'Comfortaa', cursive;
    font-size: 20px;
    letter-spacing: 1px;
    transition: all .7s ease;
    margin: 20px auto 0;
}

#enter::after {
    content: '';
    height: 15px;
    width: 15px;
    background-color: white;
    top: 60px;
    left: -25px;
    transform: translateX(0%);
    position: absolute;
    border-radius: 50%;
}

#enter::before {
    content: '';
    height: 3px;
    width: 70%;
    background-color: white;
    top: 66px;
    left: 50%;
    transform: translateX(-50%);
    position: absolute;
}

 #enter:hover {
    color: white;
     background-position: 0;
     font-size: 24px;
  }
 #enter:hover::after {
     left: 75px;
     transition: all .7s ease;
     -webkit-clip-path: polygon(100% 54%, 0 0, 0 100%);
     clip-path: polygon(100% 54%, 0 0, 0 100%);
 }

 .section-padding {

 }

  header {
        min-height: 5vh;
        padding: 2vh 5vh;
        display: flex;
      justify-content: center;
     }

     .home-screen {
       min-height: 10vh;
       padding: 10vh 1vh 5vh;
       display: block;
     }



 @media screen and (max-width: 500px) {
     .cities {
         flex-direction: column;
     }
     .cities h4 {
         display: none;
     }
     .cities ruby {
         font-size: 20px;
     }
     .cities ruby rt {
         font-size: 12px;
     }

     header {
             min-height: 10vh;
             padding: 10vh 1vh 5vh;
     }
 }