.portfolio-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    max-height: 100%;
    transition: all .5s ease;
    opacity: 1;
}

.portfolio-container.hide {
    max-height: 0;
    overflow: hidden;
    transition: all .5s ease;
    opacity: 0;
}

.portfolio-box {
    padding: 20px;
    width: calc(50% - 40px);
    min-height: 20vh;
    background-color: #282c34;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all .5s ease;
    clip-path: polygon(100% 0, 85% 0, 100% 18%, 100% 100%, 0 100%, 0% 60%, 0 0);
}

.portfolio-box1 {
    background-color: #79458D;
}

.portfolio-box2 {
    background-color: #163F7A;
}

.portfolio-box3 {
    background-color: #014B97;
}

.portfolio-box4 {
    background-color: #59ADB8;
}

.portfolio-box img{
    max-width: 500px;
    height: auto;
    width: 100%;
}

.portfolio-box:hover {
    cursor: pointer;
     opacity: 0.8;
}


.portfolio-box1-test p {
    color: white;
}


.box {
  display: block;
  width: 200px;
  height: 200px;
  background-color: gray;
  color: white;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
}

.box.green {
  background-color: green;
}

.Navbar {
    background-color: white;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    color: #3f5251;
    padding: 10px;
    min-width: 500px;
}