.About-container {
    display: flex;
    justify-content: space-evenly;
}

.About-container .portrait {
    width: 30%;
}

.About-container .portrait img {
    width: 100%;
    clip-path: circle(50% at 50% 50%);

}

.about-text-left {
    width: 30%;
    border-left: 3px solid #3f5251;
    border-top: 3px solid #3f5251;
    box-sizing: border-box;
    padding: 20px;
    line-height: 25px;
    box-shadow: 20px -19px 20px -30px;
    border-radius: 10px;
}

.about-text-right {
    width: 30%;
    border-right: 3px solid #3f5251;
    border-bottom: 3px solid #3f5251;
    box-sizing: border-box;
    border-radius: 10px;
    padding: 20px;
    line-height: 25px;
    box-shadow: 20px -19px 20px -30px;
}

.about-text-left h3, .about-text-right h3 {
    margin: 0 auto 15px;
    color: #3f5251;
    text-align: left;
}

