footer {
    min-height: 50vh;
    /*color: #e0dddd;*/
    color: white;
    overflow: hidden;
    background-color: #3f5251;
    position: relative;
    padding-bottom: 5vh;
}

.footer-standard {
    min-height: 5vh;
    padding-bottom: 0;
    background-color: white;
    color: #3f5251;
}

.footer-standard .cities {
    padding-top: 0;
}

.footer-standard h3 {
    margin-top: -15px;
    color: #3f5251;
}

footer::after {
    content: '';
    height: 4px;
    width: 100%;
    background-color: white;
    top: 10px;
    left: 0;
    position: absolute;
}

#enter {
    cursor: pointer;
    background-color: white;
    border-radius: 50%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding: 10px;
    height: 70px;
    width: 70px;
    background-image: linear-gradient(to right, #3f5251, #3f5251 50%, transparent 50%);
    background-size: 201% 100%;
    background-position: 100%;
    position: relative;
    text-decoration: none;
    color: #3f5251;
    font-family: 'Comfortaa', cursive;
    font-size: 20px;
    letter-spacing: 1px;
    transition: all .7s ease;
    margin: 20px auto 0;
}

#enter::after {
    content: '';
    height: 15px;
    width: 15px;
    background-color: white;
    top: 60px;
    left: -25px;
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    position: absolute;
    border-radius: 50%;
}

#enter::before {
    content: '';
    height: 3px;
    width: 70%;
    background-color: white;
    top: 66px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    position: absolute;
}

 #enter:hover {
    color: white;
     background-position: 0;
     font-size: 24px;
  }
 #enter:hover::after {
     left: 75px;
     transition: all .7s ease;
     -webkit-clip-path: polygon(100% 54%, 0 0, 0 100%);
     clip-path: polygon(100% 54%, 0 0, 0 100%);
 }

