nav input {
  display: none;
}
nav label {
  position: fixed;
  right: 10px;
  top: 10px;
  z-index: 999;
  cursor: pointer;
}
nav label:hover b i:nth-child(2) {
  left: 0;
}
nav label strong {
  padding: 0 10px;
  width: 40px;
  height: 40px;
  line-height: 40px !important;
  font: 700 14px 'Comfortaa', cursive;
  color: #3f5251;
  text-transform: uppercase;
  position: absolute;
  right: 40px;
  top: 0;
  transition: color 250ms ease;
}
nav label b {
  width: 40px;
  height: 40px;
  display: block;
  position: relative;
  right: 0;
  top: 0;
  cursor: pointer;
  transition: background 500ms ease;
}
nav label b i {
  display: block;
  background: #3f5251;
  width: 24px;
  height: 4px;
  position: absolute;
  left: 8px;
  top: 11px;
  border-radius: 1000px;
  -webkit-transform-origin: 20px 4px;
          transform-origin: 20px 4px;
  transition: .5s all ease;
}
nav label b i:nth-child(2) {
  top: 18px;
  transition: .5s all ease;
}
nav label b i:nth-child(3) {
  -webkit-transform-origin: 26px 4px;
          transform-origin: 26px 4px;
  top: 25px;
}
nav menu {
  background: white;
  min-width: 300px;
  max-width: 33%;
  height: 100vh;
  opacity: 0;
  overflow: auto;
  transition: all 500ms ease;
  position: fixed;
  z-index: 0;
  top: 0;
  margin: 0;
  padding: 0;
  box-shadow: -1px -1px 9px 2px rgba(136, 136, 136, 0.5);
  right: -100%;
}
nav menu li {
  margin: 0;
  list-style: none;
  width: 100%;
}
nav menu li:first-child {
  margin-top: 60px;
}
nav menu li:first-child a {
  border-top: #3f5251 1px solid;
}
nav menu li a {
  font-family: 'Comfortaa', cursive;
  text-transform: uppercase;
  text-decoration: none;
  color: #3f5251;
  display: block;
  border-bottom: #3f5251 1px solid;
  text-align: left;
  font-weight: 600;
  transition: all .4s ease;
  padding: 20px 10px;
}
nav menu li a:hover {
  /*background: rgba(0, 0, 0, 0.1);*/
    background-color: #3f5251;
    transition: .5s;
    -webkit-transform: rotateZ(2deg);
            transform: rotateZ(2deg);
    box-shadow: 18px 9px 11px -3px;
    color: white;
}
nav #nav:checked ~ label strong {

}
nav #nav:checked ~ label b {

}
nav #nav:checked ~ label b:hover {

}
nav #nav:checked ~ label b i:nth-child(1) {
  -webkit-transform: rotate(45deg) translate(6px, 11px);
          transform: rotate(45deg) translate(6px, 11px);
}
nav #nav:checked ~ label b i:nth-child(2) {
  opacity: 0;
}
nav #nav:checked ~ label b i:nth-child(3) {
  -webkit-transform: rotate(-45deg) translate(10px, -14px);
          transform: rotate(-45deg) translate(10px, -14px);
}
nav #nav:checked ~ menu {
  opacity: 1;
    right: 0;
    z-index: 9;
}

